import React, { useContext, useState } from 'react'
import { NavLink, useHistory } from 'react-router-dom'
import { UserContext } from '../App'

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Login = () => {
    const {state,dispatch}=useContext(UserContext);
    const history = useHistory()
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('')
    const loginuser = async (e) => {
        e.preventDefault();

        const res = await fetch('http://localhost:5000/signin', {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                email,
                password
            })

        })
        const data = res.json()
        console.log(data);
        if ( res.status === 400 || !data ) {
            toast("please enter complete detail!");
        } else {
            dispatch({type:'USER',payload:true})
            toast("Login successful!");
            history.push('/');
        }
    }
    return (
        <div>
            <div className="container-fluid">
                <div className="row main-content bg-success text-center">
                    <div className="col-md-4 text-center company__info">
                        <span className="company__logo"><h2><span className="fa fa-android"></span></h2></span>
                        <h4 className="company_title"><img src="data:image/png;base64," alt="companyLogo" /></h4>
                    </div>
                    <div className="col-md-8 col-xs-12 col-sm-12 login_form ">
                        <div className="container-fluid">
                            <div className="row">
                                <h2>Log In</h2>
                            </div>
                            <div className="row">
                                <form control="" className="form-group">
                                    <div className="row">
                                        <input type="email" name="email" id="email" className="form__input" placeholder="Email" value={email} onChange={(e) => { setEmail(e.target.value) }} />
                                    </div>
                                    <div className="row">
                                        
                                        <input type="password" name="password" id="password" className="form__input" placeholder="Password" value={password} onChange={(e) => { setPassword(e.target.value) }} />
                                    </div>

                                    <div className="row">
                                        <button className="btn" onClick={loginuser}>Log in </button>
                                    </div>
                                </form>
                            </div>
                            <div className="row">
                                <p>Don't have an account? <NavLink to="/signup">Register Here</NavLink></p>
                            </div>
                        </div>
                        <ToastContainer
                            position="top-center"
                            autoClose={5000}
                            hideProgressBar={false}
                            newestOnTop={false}
                            closeOnClick
                            rtl={false}
                            pauseOnFocusLoss
                            draggable
                            pauseOnHover
                        />
                    </div>
                </div>
            </div>


        </div>

    )
}

export default Login
